	module.exports = {"data":{"site":{"siteMetadata":{"title":"Logrinto Blog","subtitle":"Blog zum Interaction Designer an der Schule für Gestaltung Zürich","author":{"name":"Logrinto","twitter":"https://twitter.com/logrinto"}}},"markdownRemark":{"id":"/home/travis/build/logrinto/website/src/pages/articles/2019-07-12---Zusammenfassung/index.md absPath of file >>> MarkdownRemark","html":"<h2>Eine Klasse, vier Tage und zwölf Artikel über spezifische Aspekte responsiver Gestaltung.</h2>\n<p>Die Klasse IADHF17 an der Schule für Gestaltung Zürich hatte die Vorgabe sich in Gruppen, um diverse Aspekte der responsiven Gestaltung zu kümmern.</p>\n<p>Dabei wurden Blog-Artikel erstellt, die hier zusammengefasst zugänglich sind. Die Artikel sind jeweils innerhalb eines Tages entstanden und bieten so einen Einblick in die denkprozesse und sichtweisen der Student*innen.</p>\n<p>Der Prozess des kollaborativen Schreibens regt dazu an, bestehende Arbeitstechniken zu hinterfragen, gelerntes zu überprüfen und im Diskurs Wissen zu vertiefen oder zu verwerfen.</p>\n<p>Der Anspruch an Vollständigkeit ist irrelevant.\nViel Spass bei der Lektüre.</p>\n<p>Dozent: Boris Périsset</p>\n<h3>Die letzten Artikel</h3>\n<p>Pascale: <a href=\"https://hackmd.io/Aj1EtyoOTkKKeUeq7Clh_w?view\">https://hackmd.io/Aj1EtyoOTkKKeUeq7Clh_w?view</a>\nMatthias: <a href=\"https://hackmd.io/@matthiaskoch/B15SPjw0V\">https://hackmd.io/@matthiaskoch/B15SPjw0V</a>\nLars: <a href=\"https://hackmd.io/@larsmaeder/tabellen\">https://hackmd.io/@larsmaeder/tabellen</a>\nNils: <a href=\"https://hackmd.io/DSK1lExkS5KBJWL8RvmCqQ?view\">https://hackmd.io/DSK1lExkS5KBJWL8RvmCqQ?view</a>\nStefanie: <a href=\"https://hackmd.io/N-ml3NM5SH25Z2gDt5exXg?view\">https://hackmd.io/N-ml3NM5SH25Z2gDt5exXg?view</a>\nMarc: <a href=\"https://hackmd.io/VE3fttcJRt-yu3Vhzf9Yzg?view\">https://hackmd.io/VE3fttcJRt-yu3Vhzf9Yzg?view</a>\nMarius, Tiö: <a href=\"https://hackmd.io/@_UyE_hzgQDm18pnDxxPsRQ/HkxZYI3nE\">https://hackmd.io/@<em>UyE</em>hzgQDm18pnDxxPsRQ/HkxZYI3nE</a>\nSeverin: <a href=\"https://hackmd.io/j5AfULtWTZy-dZ53rhVt4Q\">https://hackmd.io/j5AfULtWTZy-dZ53rhVt4Q</a>\nNicole: <a href=\"https://hackmd.io/pD-O8vFwQ5CCf3dz0QAQjA?view\">https://hackmd.io/pD-O8vFwQ5CCf3dz0QAQjA?view</a>\nWolfgang: <a href=\"https://hackmd.io/-V6SpolDSw2gT0RFPODN5A\">https://hackmd.io/-V6SpolDSw2gT0RFPODN5A</a></p>\n<p>Natasha war abwesend am Polishing-Tag</p>\n<h4>Alle Artikel hier</h4>\n<ul>\n<li><a href=\"/posts/responsive-design-iad2017-prozent/\">Mit Prozent zum flüssigen Design · Marc Hatt</a></li>\n<li><a href=\"/posts/responsive-design-iad2017-codedesign/\">Code und Design · Nils Mäder</a></li>\n<li><a href=\"/posts/responsive-design-iad2017-dynamische-typografie/\">Alles bleibt anders – dynamische Typografie in digitalen Anwendungen · Natasha Ruf &#x26; Wolfgang Schoeck</a></li>\n<li><a href=\"/posts/responsive-design-iad2017-em/\">Dynamische Masseinheit em · Tieu Khe Mayer, Natasha Ruf &#x26; Marius Becker</a></li>\n<li><a href=\"/posts/responsive-design-iad2017-referenzdesign/\">Mach mal genau so! Referenzdesign · Nils Mäder, Matthias Koch, Stephanie Fuchs</a></li>\n<li><a href=\"/posts/responsive-design-iad2017-tabellen-im-web/\">Umgang mit Tabellen im Web · Lars Mäder</a></li>\n</ul>\n<p>Masseinheit zur Schriftgrösse des Elements EM-Wert\t<a href=\"https://hackmd.io/@_UyE_hzgQDm18pnDxxPsRQ/HkxZYI3nE\">https://hackmd.io/@<em>UyE</em>hzgQDm18pnDxxPsRQ/HkxZYI3nE</a></p>\n<p>Bezugselemente %\n<a href=\"https://hackmd.io/gjlmheTESDKE4qDj08Sk5A\">https://hackmd.io/gjlmheTESDKE4qDj08Sk5A</a></p>\n<p>Wann sich der Einsatz von Viewport Einheiten lohnt\n<a href=\"https://hackmd.io/pD-O8vFwQ5CCf3dz0QAQjA\">https://hackmd.io/pD-O8vFwQ5CCf3dz0QAQjA</a></p>\n<p>Mach mal genau so – Referenzdesign\t<a href=\"https://hackmd.io/KpcuL5R1SwaiEcNQy128ug\">https://hackmd.io/KpcuL5R1SwaiEcNQy128ug</a></p>\n<p>Braucht es neue Design Tools in Zukunft?\n<a href=\"https://hackmd.io/u3MuqoyGRfiqy7tRRMv-DA?view\">https://hackmd.io/u3MuqoyGRfiqy7tRRMv-DA?view</a></p>\n<p>Möglichkeiten des CSS-Grids\n<a href=\"https://hackmd.io/H-fHUgvsSy-VHJqpNjr5ag?view\">https://hackmd.io/H-fHUgvsSy-VHJqpNjr5ag?view</a></p>\n<p>Do tools really matter?\n<a href=\"https://hackmd.io/Aj1EtyoOTkKKeUeq7Clh_w\">https://hackmd.io/Aj1EtyoOTkKKeUeq7Clh_w</a></p>\n<p>Best Practice Tables\n<a href=\"https://hackmd.io/u7YoPKPoQAGzJDHizez6XQ?view\">https://hackmd.io/u7YoPKPoQAGzJDHizez6XQ?view</a></p>\n<p>Trennregeln und Grauwert\n<a href=\"https://hackmd.io/-V6SpolDSw2gT0RFPODN5A\">https://hackmd.io/-V6SpolDSw2gT0RFPODN5A</a></p>\n<p>Modular Scale\n<a href=\"https://hackmd.io/XyIGmlHBSdaMSw1YU7T8ng?view\">https://hackmd.io/XyIGmlHBSdaMSw1YU7T8ng?view</a></p>\n<p>Das perfekte Typo-Artboard\n<a href=\"https://hackmd.io/j5AfULtWTZy-dZ53rhVt4Q?view\">https://hackmd.io/j5AfULtWTZy-dZ53rhVt4Q?view</a></p>","fields":{"tagSlugs":["/tags/responsive-gestaltung/","/tags/typografie/"]},"frontmatter":{"title":"Die Sezierung responsiver Web-Gestaltung","author":"Boris Périsset","category":"Responsive Gestaltung","tags":["Responsive Gestaltung","Typografie"],"date":"2019-07-12","description":null,"featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":3,"src":"/static/header-b4df787bae39275655d0d21cc7984aa9-ef3a8.png","srcSet":"/static/header-b4df787bae39275655d0d21cc7984aa9-e0a0e.png 750w,\n/static/header-b4df787bae39275655d0d21cc7984aa9-0bb6d.png 1500w,\n/static/header-b4df787bae39275655d0d21cc7984aa9-ef3a8.png 3000w","sizes":"(max-width: 3000px) 100vw, 3000px"}}}},"wordCount":{"words":318}}},"pathContext":{"slug":"/posts/responsive-design-iad2017/"}}

